@font-face {
  font-family: 'Inziu Iosevka SC';
  src: local('Inziu Iosevka SC'), url(../fonts/inziu.ttf) format('truetype');
  font-style: normal;
}

@font-face {
  font-family: 'Noto Serif';
  font-style: normal;
  font-weight: 400;
  src: local('Noto Serif'), local('NotoSerif'),
    url(../fonts/notoserif.woff2) format('woff2');
  unicode-range: U+0000-00ff, U+0131, U+0152-0153, U+02c6, U+02da, U+02dc, U+2000-206f,
    U+2074, U+20ac, U+2212, U+2215, U+E0FF, U+EFFD, U+F000;
}
